export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <div>\n        <VToolbar\n          :dark=\"dark\"\n          :light=\"light\"\n          :absolute=\"absolute\"\n          :fixed=\"fixed\"\n          style=\"height: 100px;\"\n        >\n          <template #top-left>\n            top-left\n          </template>\n          <template #top-center>\n            top-center\n          </template>\n          <template #top-right>\n            top-right\n          </template>\n          <template #left>\n            <VToolbarHamburger\n              v-model=\"isMenuExpandedOnMobile\"\n              style=\"padding: 10px; font-size: 30px;\"\n            />\n            <VToolbarLogo\n              href=\"/\"\n              src=\"https://via.placeholder.com/350x60?text=logo\"\n              alt=\"Home page\"\n            />\n            Left side\n          </template>\n          <template #center>\n            Center side\n          </template>\n          <template #right>\n            <VToolbarMenu\n              :dark=\"dark\"\n              :light=\"light\"\n              :is-expanded-on-mobile=\"isMenuExpandedOnMobile\"\n              @outside=\"closeAll\"\n            >\n              <VToolbarMenuItem href=\"/\">\n                Link\n              </VToolbarMenuItem>\n              <VToolbarMenuDropdown\n                v-model=\"isActiveDropdown1\"\n                active-color=\"primary\"\n                active-text-color=\"white\"\n              >\n                Dropdown 1\n                <template #dropdown>\n                  <div style=\"width: 300px; border: 1px solid black; background-color: white;padding: 10px;\">\n                    To left\n                  </div>\n                </template>\n              </VToolbarMenuDropdown>\n              <VToolbarMenuDropdown\n                v-model=\"isActiveDropdown2\"\n                position=\"right\"\n                active-color=\"secondary\"\n                active-text-color=\"white\"\n              >\n                Dropdown 2\n                <template #dropdown>\n                  <div style=\"width: 400px; border: 1px solid black; background-color: white;padding: 10px;\">\n                    To right\n                  </div>\n                </template>\n              </VToolbarMenuDropdown>\n              <VToolbarMenuDropdown\n                :model-value=\"isActiveDropdown3\"\n                active-color=\"white\"\n                active-text-color=\"primary\"\n                position=\"fullsize\"\n                @open=\"isActiveDropdown3 = true; isActiveDropdown1 = false; isActiveDropdown2 = false\"\n                @close=\"isActiveDropdown3 = false\"\n              >\n                Dropdown 3\n                <template #dropdown>\n                  <div style=\"border: 1px solid black; background-color: white;padding: 10px;\">\n                    Full size\n                  </div>\n                </template>\n              </VToolbarMenuDropdown>\n            </VToolbarMenu>\n            Right side\n          </template>\n        </VToolbar>\n        <div\n          style=\"\n            height:200px;\n            width: 100%;\n            background-color: grey;\n            z-index: 2;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n          \"\n        >\n          Content with absolute position and z-index 2\n        </div>\n      </div>\n    </Preview>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  VToolbar,\n  VToolbarLogo,\n  VToolbarMenu,\n  VToolbarMenuItem,\n  VToolbarMenuDropdown,\n  VToolbarHamburger\n} from '@/components';\nimport {\n  Preview,\n  Sandbox\n} from '@/docs/components';\n\nexport default {\n  name: 'ToolbarExample',\n  components: {\n    Preview,\n    Sandbox,\n    VToolbar,\n    VToolbarLogo,\n    VToolbarMenu,\n    VToolbarMenuItem,\n    VToolbarMenuDropdown,\n    VToolbarHamburger\n  },\n  data () {\n    return {\n      dark: true,\n      light: false,\n      absolute: false,\n      fixed: false,\n      isActiveDropdown1: false,\n      isActiveDropdown2: true,\n      isActiveDropdown3: false,\n      isMenuExpandedOnMobile: false\n    };\n  },\n  methods: {\n    closeAll () {\n      this.isActiveDropdown1 = false;\n      this.isActiveDropdown2 = false;\n      this.isActiveDropdown3 = false;\n    }\n  }\n};\n</script>\n";